/*Slogan*/

.sidebar__slogan {
  /*margin: 4px 0;*/
  margin: 0;
  margin-top: 2px;
  margin-bottom: 24px;
  font-size: 12px;
  color: hsl(0, 0%, 30%);
}

/*Language*/

.sidebar__lang-switcher {
  margin: 36px 0;
}

/*Toggle*/

.sidebar__toggle-checkbox {
  position: absolute;
  top: 0;
  right: 0;
  margin: 0;
  width: 72px;
  height: 72px;

  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  border-radius: 0;
}

.sidebar__toggle-checkbox:not(:checked) ~ .sidebar__menu {
  display: none;
}

.sidebar__toggle-label {
  position: absolute;
  top: 27px;
  right: 27px;
  width: 18px;
  height: 18px;
}
.sidebar__toggle-label::before,
.sidebar__toggle-label::after {
  content: "";
  position: absolute;
  display: block;
  left: 0;
  right: 0;
  height: 2px;
  background-color: black;
  transition: transform 0.3s ease, opacity 0.3s ease;
}
.sidebar__toggle-label::before {
  top: 5px;
}
.sidebar__toggle-label::after {
  bottom: 5px;
}

.sidebar__toggle-checkbox:checked ~ .sidebar__toggle-label::before {
  top: 8px;
  transform: rotate(45deg);
}
.sidebar__toggle-checkbox:checked ~ .sidebar__toggle-label::after {
  bottom: 8px;
  transform: rotate(-45deg);
}

/*Nav*/

.sidebar__nav {
  /*padding-bottom: 14px;*/
  margin-bottom: 8px;
  padding-bottom: 8px;
  border-bottom: 1px solid var(--color-neutral);
}

.sidebar__nav-link {
  display: inline-block;
  width: 100%;
  font-size: var(--font-big);
  line-height: 36px;
}

/*Content*/

.content {
  padding: 24px;
  padding-top: 0;
}

.content__article {
  max-width: 700px;
}

/*Gallery*/

.gallery {
  display: flex;
  flex-wrap: wrap;
  margin: -4px;
  max-height: 700px;
  overflow: hidden;
  /*align-content: stretch;*/
}

@media (min-width: 1260px) {
  .gallery {
    max-height: 350px;
  }
}

.gallery__item {
  width: 150px;
  height: 350px;
  padding: 4px;
  /*border: 1px solid black;*/
  flex-grow: 1;
  box-sizing: border-box;
}

.gallery__item--empty {
  height: 0;
  padding: 0;
}

.gallery__photo {
  display: block;
  height: 250px;
  background-image: url("https://photo.cdn.1st-social.com/1000987/1000987300-78-2542809742727000459.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top center;
  border-radius: 4px;
}

.gallery__info {
  padding: 8px 0;
  line-height: 20px;
}

.gallery__status {
  float: left;
  margin: 4px 4px 4px 0;
}

.gallery__city {
  display: none;
}

.gallery__name {
  float: left;
  font-weight: bold;
}

.gallery__age::before {
  content: ", "
}

/*Status*/

.status {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: var(--color-neutral);
}
.status--online {
  background-color: limegreen;
}

/*Footer*/

.footer {
  margin: 48px 0;
}

.footer__text {
  margin: 24px 0;
  /*font-size: 14px;*/
  color: hsl(0, 0%, 30%);
}

.footer__text--ukraine {
  position: relative;
  margin: 48px 0;
  padding-left: 48px;
  /*padding-top: 48px;*/
  /*padding-bottom: 48px;*/
  border-top: 1px solid hsl(0, 0%, 90%);
  border-bottom: 1px solid hsl(0, 0%, 90%);
}
.footer__text--ukraine::before {
  content: "";
  position: absolute;
  top: 1em;
  left: 0;
  width: 36px;
  border-top: 14px solid #005BBB;
  border-bottom: 14px solid #FFD500;
}

.footer__nav {
  margin: 24px 0;
  /*padding-bottom: 24px;*/
  font-size: 0;
  /*border-bottom: 1px solid hsl(0, 0%, 90%);*/
}

.footer__nav-item {
  display: inline-block;
  font-size: 14px;
}

.footer__nav-item:not(:last-child)::after {
  content: "|";
  padding: 0 12px;
  color: hsl(0, 0%, 30%);
  line-height: 1.5em;
}

.footer__nav-link {
  text-wrap: nowrap;
  color: black;
  opacity: 0.75;
}

/*Toolbar*/

.toolbar {
  /*text-align: right;*/
  /*margin-top: 14px;*/
}

.toolbar__link {
  display: block;
  font-size: var(--font-big);
  display: block;
  line-height: 36px;
}

.toolbar__link--strong {
  font-weight: bold;
}

.layout__help {
  width: 60px;
  height: 60px;
  background-color: var(--color-secondary);
  /*background-color: hsl(35, 100%, 65%);*/
  /*color: black;*/
  color: white;
  font-size: 24px;
  /*font-weight: bold;*/
  text-align: center;
  line-height: 60px;
  border-radius: 50%;
  /*box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);*/
  cursor: pointer;
  transition: background-color 0.1s ease;
}

.layout__help:hover {
  opacity: 0.7;
}

@media (min-width: 768px) {
  .sidebar {
    width: 300px;
    flex-shrink: 0;
    background-color: hsl(211, 10%, 95%);
  }

  .sidebar__logo {
    /*width: 200px;*/
    /*height: 53px;*/
  }

  .sidebar__toggle-checkbox:not(:checked) ~ .sidebar__menu {
    display: block;
  }

  .sidebar__toggle-checkbox,
  .sidebar__toggle-label {
    display: none;
  }

  .sidebar__nav {
    border-bottom: none;
  }

  .toolbar {
    text-align: right;
  }

  .toolbar__link {
    display: inline-block;
    margin-left: 12px;
    line-height: 48px;
  }

  .content {
    flex-grow: 1;
    padding: 24px 64px;
  }
}

