.sidebar__toggle-checkbox {
  appearance: none;
  border-radius: 0;
  width: 72px;
  height: 72px;
  margin: 0;
  position: absolute;
  top: 0;
  right: 0;
}

.sidebar__toggle-checkbox:not(:checked) ~ .sidebar__menu {
  display: none;
}

.sidebar__toggle-label {
  width: 18px;
  height: 18px;
  position: absolute;
  top: 27px;
  right: 27px;
}

.sidebar__toggle-label:before, .sidebar__toggle-label:after {
  content: "";
  background-color: #000;
  height: 2px;
  transition: transform .3s, opacity .3s;
  display: block;
  position: absolute;
  left: 0;
  right: 0;
}

.sidebar__toggle-label:before {
  top: 5px;
}

.sidebar__toggle-label:after {
  bottom: 5px;
}

.sidebar__toggle-checkbox:checked ~ .sidebar__toggle-label:before {
  top: 8px;
  transform: rotate(45deg);
}

.sidebar__toggle-checkbox:checked ~ .sidebar__toggle-label:after {
  bottom: 8px;
  transform: rotate(-45deg);
}

