/*
 * –û–ø—Ä–µ–¥–µ–ª–µ–Ω–∏–µ –≥–ª–æ–±–∞–ª—å–Ω—ã—Ö CSS-–ø–µ—Ä–µ–º–µ–Ω–Ω—ã—Ö (custom properties) –¥–ª—è –æ—Å–Ω–æ–≤–Ω—ã—Ö
 * —Ü–≤–µ—Ç–æ–≤, –æ—Ç—Å—Ç—É–ø–æ–≤ –∏ —Ä–∞–∑–º–µ—Ä–æ–≤, –∫–æ—Ç–æ—Ä—ã–µ –∏—Å–ø–æ–ª—å–∑—É—é—Ç—Å—è –ø–æ –≤—Å–µ–º—É –ø—Ä–æ–µ–∫—Ç—É.
 * –î–æ–ø–æ–ª–Ω–∏—Ç–µ–ª—å–Ω—ã–µ –ø–µ—Ä–µ–º–µ–Ω–Ω—ã–µ –¥–ª—è —Å–ø–µ—Ü–∏—Ñ–∏—á–Ω—ã—Ö –±–ª–æ–∫–æ–≤ –º–æ–≥—É—Ç –±—ã—Ç—å –æ–ø—Ä–µ–¥–µ–ª–µ–Ω—ã –≤
 * —Å–æ–æ—Ç–≤–µ—Ç—Å—Ç–≤—É—é—â–∏—Ö —Ñ–∞–π–ª–∞—Ö —Å—Ç–∏–ª–µ–π.
 */

:root {
  /* –¶–≤–µ—Ç–∞ */
